  cursor: pointer;
  border-radius: 5px;
  padding: 15px 32px;
  display: inline-block;
  -webkit-transition: color 0.1s, border-color 0.1s, background-color 0.1s;
  transition: color 0.1s, border-color 0.1s, background-color 0.1s; }
  .theme-light .app-container .button {
